.page-footer {
  width: 100%;

  &__wrapper {
    @extend .wrapper;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }

  &__logo {
    width: 42px;
    height: 42px;
    margin-top: 2px;
    outline: 0;
  }

  &__left-block {
    display: flex;
    flex-direction: column;
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-end;


    &-first {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
    }
  }

  &__instagram {
    display: inline-flex;
    width: 12px;
    height: 12px;
    margin-left: 8px;
    background: url('../img/icon-instagram.svg') no-repeat center center / 100% 100%;
    outline: 0;
  }

}
