html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  color: inherit;
  text-decoration: none;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  font-size: inherit;
}

ul {
  list-style: none;
}

input {
  outline: 0;
}

input[type="radio"],
input[type="checkbox"],
.visually-hidden:not(:focus):not(:active) {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

button {
  outline: 0;
}

.hide-mobile {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
  }
}

.wrapper {
  width: 90%;
  min-width: 290px;
  max-width: 668px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 87%;
    max-width: 1000px;
  }

  @media (min-width: $desktop-width) {
    width: 84.5%;
    max-width: 1500px;
  }
}

@mixin wrapper-reset {
  width: auto;
  min-width: 0;
  max-width: none;
  margin: 0;
  padding: 0;
}
